  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.filters {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  right: 0;
  left: 0;
}
.filters li {
  display: inline;
}
.filters li a {
  color: inherit;
  margin: 3px;
  padding: 3px 7px;
  text-decoration: none;
  border: 1px solid transparent;
  border-radius: 3px;
}
.filters li a:hover {
  border-color: rgba(175, 47, 47, 0.1);
}
.filters li a.selected {
  border-color: rgba(175, 47, 47, 0.2);
}

/*  content */
#main .g-content{
  position: relative;
  margin: 0 auto;
  width: 100%;
  -webkit-box-shadow: 0 3px 3px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 0 3px 3px 2px rgba(0, 0, 0, 0.25);
}
#main .g-content .g-tasklist{
  position: relative;
  z-index: 6;
}
.g-content .g-tasklist .m-tasks{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  font-size:24px;
  padding:8px;
  background: #fff;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.u-check{
  width:24px;
  height:24px;
  margin-left:10px;
}
.u-message{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  text-align: left;
  margin-left: 16px;
  font-size: 20px;
  padding: 6px 0;
  word-break: break-all;
  display: block;
  line-height: 1.2;
  -webkit-transition: color 0.4s;
  transition: color 0.4s;
}
.u-update{
  position: relative;
  z-index:1;
}
.u-close{
  position: absolute;
  right: 16px;
  cursor: pointer;
  width: 24px;
  height: 24px;
}
.u-close-icon{
  background:url(data:image/png;base64,) no-repeat;
}

